import {
  forwardRef,
  Inject,
  Directive,
  Input,
  Output,
  EventEmitter,
  ElementRef,
  ContentChild,
  NgZone,
  Renderer2,
  OnInit,
  OnDestroy,
  ChangeDetectorRef,
  ContentChildren,
  AfterViewInit
} from '@angular/core';
import {DOCUMENT} from '@angular/common';
import {fromEvent, race, Subject, Subscription, merge} from 'rxjs';
import {filter, takeUntil} from 'rxjs/operators';
import {NgbDropdownConfig} from './dropdown-config';
import {positionElements, PlacementArray, Placement} from '../util/positioning';
import {Key} from '../util/key';

/**
 */
@Directive({selector: '.dropdown-item:not(.disabled)'})
export class NgbDropdownItem {
  dropdownItemEl: HTMLElement;

  @Input('disabled') private _disabled;

  constructor(private _elementRef: ElementRef<HTMLElement>) { this.dropdownItemEl = _elementRef.nativeElement; }

  get disabled(): boolean { return this._disabled != null; }
}

/**
 */
@Directive({
  selector: '[ngbDropdownMenu]',
  host: {'[class.dropdown-menu]': 'true', '[class.show]': 'dropdown.isOpen()', '[attr.x-placement]': 'placement'}
})
export class NgbDropdownMenu {
  placement: Placement = 'bottom';
  isOpen = false;

  @ContentChildren(NgbDropdownItem) menuItems: NgbDropdownItem[];

  constructor(
      @Inject(forwardRef(() => NgbDropdown)) public dropdown, private _elementRef: ElementRef<HTMLElement>,
      private _renderer: Renderer2) {}

  isEventFrom($event) { return this._elementRef.nativeElement.contains($event.target); }

  position(triggerEl, placement) {
    this.applyPlacement(positionElements(triggerEl, this._elementRef.nativeElement, placement));
  }

  applyPlacement(_placement: Placement) {
    // remove the current placement classes
    this._renderer.removeClass(this._elementRef.nativeElement.parentNode, 'dropup');
    this._renderer.removeClass(this._elementRef.nativeElement.parentNode, 'dropdown');
    this.placement = _placement;
    /**
     * apply the new placement
     * in case of top use up-arrow or down-arrow otherwise
     */
    if (_placement.search('^top') !== -1) {
      this._renderer.addClass(this._elementRef.nativeElement.parentNode, 'dropup');
    } else {
      this._renderer.addClass(this._elementRef.nativeElement.parentNode, 'dropdown');
    }
  }
}

/**
 * Marks an element to which dropdown menu will be anchored. This is a simple version
 * of the NgbDropdownToggle directive. It plays the same role as NgbDropdownToggle but
 * doesn't listen to click events to toggle dropdown menu thus enabling support for
 * events other than click.
 *
 * @since 1.1.0
 */
@Directive({
  selector: '[ngbDropdownAnchor]',
  host: {'class': 'dropdown-toggle', 'aria-haspopup': 'true', '[attr.aria-expanded]': 'dropdown.isOpen()'}
})
export class NgbDropdownAnchor {
  anchorEl;

  constructor(@Inject(forwardRef(() => NgbDropdown)) public dropdown, private _elementRef: ElementRef<HTMLElement>) {
    this.anchorEl = _elementRef.nativeElement;
  }

  isEventFrom($event) { return this._elementRef.nativeElement.contains($event.target); }
}

/**
 * Allows the dropdown to be toggled via click. This directive is optional: you can use NgbDropdownAnchor as an
 * alternative.
 */
@Directive({
  selector: '[ngbDropdownToggle]',
  host: {
    'class': 'dropdown-toggle',
    'aria-haspopup': 'true',
    '[attr.aria-expanded]': 'dropdown.isOpen()',
    '(click)': 'toggleOpen()'
  },
  providers: [{provide: NgbDropdownAnchor, useExisting: forwardRef(() => NgbDropdownToggle)}]
})
export class NgbDropdownToggle extends NgbDropdownAnchor {
  constructor(@Inject(forwardRef(() => NgbDropdown)) dropdown, elementRef: ElementRef<HTMLElement>) {
    super(dropdown, elementRef);
  }

  toggleOpen() { this.dropdown.toggle(); }
}

/**
 * Transforms a node into a dropdown.
 */
@Directive({selector: '[ngbDropdown]', exportAs: 'ngbDropdown', host: {'[class.show]': 'isOpen()'}})
export class NgbDropdown implements AfterViewInit, OnInit, OnDestroy {
  private _closed$ = new Subject<void>();
  private _zoneSubscription: Subscription;

  @ContentChild(NgbDropdownMenu) private _menu: NgbDropdownMenu;

  @ContentChild(NgbDropdownMenu, {read: ElementRef}) private _menuElementRef: ElementRef<HTMLElement>;

  @ContentChild(NgbDropdownAnchor) private _anchor: NgbDropdownAnchor;

  /**
   * Indicates that dropdown should be closed when selecting one of dropdown items (click) or pressing ESC.
   * When it is true (default) dropdowns are automatically closed on both outside and inside (menu) clicks.
   * When it is false dropdowns are never automatically closed.
   * When it is 'outside' dropdowns are automatically closed on outside clicks but not on menu clicks.
   * When it is 'inside' dropdowns are automatically on menu clicks but not on outside clicks.
   */
  @Input() autoClose: boolean | 'outside' | 'inside';

  /**
   *  Defines whether or not the dropdown-menu is open initially.
   */
  @Input('open') _open = false;

  /**
   * Placement of a popover accepts:
   *    "top", "top-left", "top-right", "bottom", "bottom-left", "bottom-right",
   *    "left", "left-top", "left-bottom", "right", "right-top", "right-bottom"
   * and array of above values.
   */
  @Input() placement: PlacementArray;

  /**
   *  An event fired when the dropdown is opened or closed.
   *  Event's payload equals whether dropdown is open.
   */
  @Output() openChange = new EventEmitter();

  constructor(
      private _changeDetector: ChangeDetectorRef, config: NgbDropdownConfig, @Inject(DOCUMENT) private _document: any,
      private _ngZone: NgZone) {
    this.placement = config.placement;
    this.autoClose = config.autoClose;
    this._zoneSubscription = _ngZone.onStable.subscribe(() => { this._positionMenu(); });
  }

  ngOnInit() {
    if (this._menu) {
      this._menu.applyPlacement(Array.isArray(this.placement) ? (this.placement[0]) : this.placement as Placement);
    }

    if (this._open) {
      this._setCloseHandlers();
    }
  }

  ngAfterViewInit() { this._setKeyboardHandlers(); }

  /**
   * Checks if the dropdown menu is open or not.
   */
  isOpen(): boolean { return this._open; }

  /**
   * Opens the dropdown menu of a given navbar or tabbed navigation.
   */
  open(): void {
    if (!this._open) {
      this._open = true;
      this._positionMenu();
      this.openChange.emit(true);
      this._setCloseHandlers();
    }
  }

  private _setCloseHandlers() {
    if (this.autoClose) {
      this._ngZone.runOutsideAngular(() => {
        const escapes$ = fromEvent<KeyboardEvent>(this._document, 'keyup')
                             .pipe(takeUntil(this._closed$), filter(event => event.which === Key.Escape));

        const clicks$ = fromEvent<MouseEvent>(this._document, 'click')
                            .pipe(takeUntil(this._closed$), filter(event => this._shouldCloseFromClick(event)));

        race<Event>([escapes$, clicks$]).pipe(takeUntil(this._closed$)).subscribe(() => this._ngZone.run(() => {
          this.close();
          this._changeDetector.markForCheck();
        }));
      });
    }
  }

  /**
   * Closes the dropdown menu of a given navbar or tabbed navigation.
   */
  close(): void {
    if (this._open) {
      this._open = false;
      this._closed$.next();
      this.openChange.emit(false);
    }
  }

  /**
   * Toggles the dropdown menu of a given navbar or tabbed navigation.
   */
  toggle(): void {
    if (this.isOpen()) {
      this.close();
    } else {
      this.open();
    }
  }

  private _shouldCloseFromClick(event: MouseEvent) {
    if (event.button !== 2 && !this._isEventFromToggle(event)) {
      if (this.autoClose === true) {
        return true;
      } else if (this.autoClose === 'inside' && this._isEventFromMenu(event)) {
        return true;
      } else if (this.autoClose === 'outside' && !this._isEventFromMenu(event)) {
        return true;
      }
    }
    return false;
  }

  ngOnDestroy() {
    this._closed$.next();
    this._zoneSubscription.unsubscribe();
  }

  private _isEventFromToggle($event) { return this._anchor.isEventFrom($event); }

  private _isEventFromMenu($event) { return this._menu ? this._menu.isEventFrom($event) : false; }

  private _positionMenu() {
    if (this.isOpen() && this._menu) {
      this._menu.position(this._anchor.anchorEl, this.placement);
    }
  }

  private _setKeyboardHandlers() {
    const listeners = [];
    if (this._anchor) {
      const anchor$ = fromEvent<KeyboardEvent>(this._anchor.anchorEl, 'keydown').pipe(filter(filterKeyboardEvents));
      listeners.push(anchor$);
    }
    if (this._menu) {
      const items$ =
          merge(...this._menu.menuItems.map(item => fromEvent<KeyboardEvent>(item.dropdownItemEl, 'keydown')))
              .pipe(filter(filterKeyboardEvents));
      listeners.push(items$);
    }

    merge(...listeners).subscribe(this.onKeyDown.bind(this));
  }

  onKeyDown(event: KeyboardEvent) {
    if (!this.isOpen()) {
      this.open();
    }
    const list = this._getMenuElements();

    if (list.length === 0) {
      return;
    }

    let position = list.findIndex(element => element === this._document.activeElement);
    switch (event.which) {
      case Key.ArrowDown:
        position = Math.min(position + 1, list.length - 1);
        break;
      case Key.ArrowUp:
        position = Math.max(position - 1, 0);
        break;
      case Key.Home:
        position = 0;
        break;
      case Key.End:
        position = list.length - 1;
        break;
    }
    const elm: HTMLElement = list[position];
    elm.focus();

    event.preventDefault();
  }

  private _getMenuElements(): HTMLElement[] {
    if (this._menu == null) {
      return [];
    }
    return this._menu.menuItems.filter(item => !item.disabled).map(item => item.dropdownItemEl);
  }
}

function filterKeyboardEvents(event: KeyboardEvent): boolean {
  return event.which === Key.ArrowDown || event.which === Key.ArrowUp || event.which === Key.Home ||
      event.which === Key.End;
}
